import React, { useMemo } from 'react'
import { SlideUpAnimation } from '../../components'
import { CONTENT } from '../../config/content'
interface Technology {
    name: string
    rating: number
}
export function AboutCard() {
    const technologies = useMemo<Technology[]>(() => {
        return CONTENT.about.frameworks
    }, [])
    return (
        <div className="about-card">
            <section className="content">
                <p>{CONTENT.about.description.paragraph1}</p>
                <p>{CONTENT.about.description.paragraph2}</p>
                <p>{CONTENT.about.description.paragraph3}</p>
            </section>
            <section className="footer">
                <ol className="technologies-list">
                    {technologies.map((technology) => (
                        <li className="technology-item">{technology.name}</li>
                    ))}
                </ol>
            </section>
        </div>
    )
}
