import React from 'react'
import { redirect } from '../../utils'
import { IOrganization } from './types'
export function ExperienceCard({
    longName: name,
    designation,
    url,
    from,
    to,
    experiences
}: IOrganization) {
    return (
        <div className="experience-card">
            <section className="title">
                <span className="designation">{designation} </span>
                <span className="organization hover-underline-animation" onClick={() => redirect(url)}>@ {name}</span>
            </section>
            <section className="sub-title">{`${from}-${to}`}</section>
            <section className="content">
                <ol>
                    {experiences.map((exp) => (
                        <li className="content-item">{exp}</li>
                    ))}
                </ol>
            </section>
            <section className="footer">
                <ol className="technologies-list">
                    {/* {technologies.map((technology) => (
                        <li className="technology-item">{technology.name}</li>
                    ))} */}
                </ol>
            </section>
        </div>
    )
}
